// Digital Heritage Browse and General Browse.

.browse {
  // Default view mode is used in the Realted Content field on Collections.
  &__default {
    &__header {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      column-gap: var(--v-space-xs);

      @include media('>=md') {
        flex-direction: row;
      }

      &__image {
        flex: 1;
      }

      &__text {
        flex: 2;

        h2 {
          font-size: var(--font-size-2xxl);
          margin-block-start: 0;
        }

        > *:not(:last-child) {
          padding-block-end: var(--v-space-5xs);
        }

        &__content-type {
          font-size: var(--font-size-base);
          font-weight: var(--text-bold);
        }

        &__communities {
          .field-reference {
            display: flex;
            column-gap: var(--v-space-4xxs);

            .field-title {
              font-size: var(--font-size-base);
              font-weight: var(--text-bold);

              &::after {
                content: ':';
              }
            }

            .field-item {
              &:not(:last-of-type)::after {
                content: ',';
              }

              &__container {
                display: flex;
                flex-wrap: wrap;
                column-gap: var(--v-space-5xs);
              }
            }
          }
        }

        &__category {
          .field-reference {
            display: flex;
            column-gap: var(--v-space-4xxs);

            .field-title {
              font-size: var(--font-size-base);
              font-weight: var(--text-bold);

              &::after {
                content: ':';
              }
            }

            .field-item {
              &:not(:last-of-type)::after {
                content: ',';
              }

              &__container {
                display: flex;
                flex-wrap: wrap;
                column-gap: var(--v-space-5xs);
              }
            }
          }
        }
      }
    }
  }

  // Browse Digital Heritage page (this part sets up the grid).
  &__main-content {
    @include layout--full-width-grid;

    // Add region to the main grid,
    //then create a grid in it to add items to.
    .region {
      grid-column: 1 / -1;
      @include layout--full-width-grid-no-padding;
    }

    #block-mukurtu-v4-content {
      grid-column: 1 / -1;

      @include media('>=2xl') {
        grid-column: 2 / -2;
      }
    }

    // This is bound by the grid containing it, but also needs
    // a grid to attach its children to.
    .browse-container {
      display: grid;
      grid-template-columns: var(--grid-9col);
      column-gap: var(--gutter);

      @include media('>=xl') {
        grid-template-columns: var(--grid-12col);
      }

      .browse-links {
        margin-block: var(--v-space-4xxs);
        text-align: center;
        display: flex;
        gap: var(--v-space-5xs);

        @include media('>=lg') {
          margin-block-start: 0;
          text-align: start;
        }

        // Style switcher buttons.
        a,
        button {
          background-color: var(--color-white);
          border: 1px solid var(--brand-primary-dark);
          color: var(--brand-primary-dark);
          font-weight: var(--text-bold);
          line-height: normal;
          text-decoration: none;
          padding-block: var(--v-space-6xs);
          padding-inline: var(--v-space-5xs);
          letter-spacing: normal;

          &:hover {
            background-color: var(--brand-primary-dark);
            color: var(--color-white);
          }

          &:focus {
            outline-offset: 1px;
          }
        }

        .active-toggle {
          background-color: var(--brand-primary-dark);
          color: var(--color-white);

          &:hover {
            background-color: var(--brand-primary-accent);
          }
        }
      }

      .browse-results {
        grid-row: 2;
        grid-column: 1 / -1;

        @include media('>=lg') {
          grid-row: 1;
          grid-column: 1 / span 6;
        }

        @include media('>=xl') {
          grid-column: 1 / span 9;
        }

        // Space between main content and footer.
        margin-block-end: var(--v-separation);

        // Space between the rows.
        .views-row {
          &:first-of-type {
            border-block-start: 5px solid var(--mukurtu-view-row-divider-color);
            margin-block-start: var(--v-space-3xs);
          }

          border-block-end: 5px solid var(--mukurtu-view-row-divider-color);
        }

        // When the Grid button is selected on the Browse Digital Heritage page,
        // a masonry grid layout is applied.
        .browse-content.grid {
          column-count: 1;

          @include media('>=lg') {
            column-count: 2;
            column-gap: var(--v-space-3xs);
          }

          @include media('>=xl') {
            column-gap: var(--v-space-2xs);
          }

          @include media('>=2xl') {
            column-count: 3;
          }

          .views-row {
            break-inside: avoid;
            border-block-start: 0;
          }
        }
      }

      .browse-facets {
        grid-row: 1;
        grid-column: 1 / -1;

        @include media('>=lg') {
          grid-column: 7 / span 3;
        }

        @include media('>=xl') {
          grid-column: 10 / span 3;
        }

        // Search textfield.
        .js-form-type-textfield {
          margin-block-end: var(--v-space-4xs);
        }

        // Sort dropdown.
        .js-form-type-select {
          &::after {
            top: unset;
            bottom: 20px;
          }

          select {
            margin-inline-start: 0;
          }
        }

        // Search box at the top of the sidebar.
        .views-exposed-form {
          margin-block-end: var(--v-space-3xs);

          label {
            display: block;
            background-color: var(--brand-primary-dark);
            color: var(--light-text-color);
            font-size: var(--font-size-m);
            font-weight: var(--text-bold);
            margin-block-start: 0;
            padding-inline: var(--v-space-4xxs);
            padding-block: var(--v-space-5xs);
          }
        }

        // Details/summary elements in sidebar; facets.
        .facets-widget-checkbox {
          background-color: var(--mukurtu-panel-pane-content-background-color);
          margin-block-end: var(--v-space-4xs);

          summary {
            background-color: var(--brand-primary-dark);
            color: var(--light-text-color);
            font-size: var(--font-size-m);
            font-weight: var(--text-bold);
            margin-block-start: 0;
            padding-inline: var(--v-space-4xxs);
            padding-block: var(--v-space-5xs);
            cursor: pointer;

            &::marker {
              content: '';
            }
          }

          ul {
            padding: 0 var(--v-space-4xxs) var(--v-space-4xxs);

            li {
              list-style: none;
            }
          }
        }
      }
    }
  }

  .pager {
    // Unordered list.
    &__items {
      display: flex;
      gap: var(--v-space-4xxs);
      padding: 0;
      justify-content: center;
    }

    // List item.
    &__item {
      list-style: none;
      font-weight: var(--text-bold);

      a {
        padding: var(--v-space-5xs);
      }
    }

    .is-active {
      a {
        color: var(--brand-text-color);
        text-decoration: none;
        border-bottom: 5px solid var(--brand-secondary);
      }
    }
  }
}

// All the extra facets blocks that are placed to ensure
// each display type gets its facets working properly
// are hidden here. 

.other-facets {  
  display: none;
}